import Vue from 'vue'
import VueRouter from 'vue-router'
import stopwatch from '../components/stopwatch'
// import stopwatchcopy from '../components/stopwatch-copy'
import countdown from '../components/countdown'

Vue.use(VueRouter)

const worldTime={
  template:`<h1>还未完成~~~~~</h1>`
}
const alarmClock={
  template:`<h1>还未完成~~~~~</h1>`
}
const routes=[
  {path:'/stopwatch',component:stopwatch},
  {path:'/countdown',component:countdown},
  {path:'/worldTime',component:worldTime},
  {path:'/alarmClock',component:alarmClock}
]


export default new VueRouter({
  routes,
  linkActiveClass:'active'
})



