<template>
  <div class="outer">
    <div class="title-wrapper">
      <span class="title">秒表</span>
    </div>
    <div class="big-wrapper">
      <div class="total-time-wrapper">
        <div class="total-time">
          <div class="layout">
            <span style="font-size: 60px">{{toTimeString(totalSeconds)}}</span>
          </div>
        </div>
      </div>
      <div class="current-time-wrapper">
        <div class="current-time">
          <div class="layout">
            <button @click="start" v-show="!isStart">启动</button>
            <button @click="pause" v-show="isStart">暂停</button>
            <span>{{toTimeString(currentSeconds)}}</span>
            <button @click="count" v-show="!isCount" :disabled="totalSeconds===0">计次</button>
            <button @click="reset" v-show="isCount">复位</button>
          </div>
        </div>
      </div>
      <div class="count-wrapper">
        <div class="count">
            <table class="table">
              <tr class="thead">
                <td class="td">计次</td>
                <td class="td">单次</td>
                <td class="td">累计</td>
              </tr>
              <tr v-for="(item,index) in indexListL">
                <td>{{item}}</td>
                <td>{{toTimeString(countList[index])}}</td>
                <td>{{toTimeString(totalList[index])}}</td>
              </tr>
            </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
        return {
          totalSeconds:0,
          currentSeconds:0,
          isStart:false,
          isCount:false,
          currentIndex:1,
          countList:[],
          totalList:[],
          indexListL:[],
          timer:''
        }
    },
    watch:{
      totalSeconds(){
          if (this.totalSeconds!==0){
            this.currentSeconds+=10
          }
      }
    },
    methods:{
      start(){
        this.isStart=true
        this.isCount=false
        this.timer=setInterval(()=>{
            this.totalSeconds+=10
        },10)
      },
      pause(){
        this.isStart=false
        this.isCount=true
        clearInterval(this.timer)
      },
      count(){
        this.countList.unshift(this.currentSeconds)
        this.totalList.unshift(this.totalSeconds)
        this.indexListL.unshift(this.currentIndex++)
        this.currentSeconds=0
      },
      reset(){
          this.totalSeconds=0,
          this.currentSeconds=0,
          this.isStart=false,
          this.isCount=false,
          this.currentIndex=1,
          this.countList=[],
          this.totalList=[],
          this.indexListL=[]
      },
      toTimeString(milliseconds){
          let millisecond=milliseconds%1000/10
          let totalSecond=Math.floor(milliseconds/1000)
          let second=totalSecond%60
          let minute=Math.floor(totalSecond/60)
          millisecond=millisecond>10?millisecond:'0'+millisecond,
          second=second>10?second:'0'+second,
          minute=minute>10?second:'0'+minute
        return minute+':'+second+'.'+millisecond
      }
    },
  }
</script>

<style lang="stylus" rel="stylesheet">
  .title-wrapper
    height 60px
    width 100%
    background-color grey
    text-align center
    .title
      font-size 18px
      font-weight 700
      line-height 60px
  .big-wrapper
    display flex
    position absolute
    width 100%
    top 60px
    bottom 80px
    flex-direction column
    background-color #bcc0c8
    .layout
      display table-cell
      vertical-align middle
      text-align center
    .total-time-wrapper
      flex 1 0
      border-bottom 1px solid black
      background-color #ffffff
      .total-time
        display table
        font-size 60px
        width 100%
        height 100%
    .current-time-wrapper
      flex 1 0
      border-bottom 1px solid black
      .current-time
        display table
        font-size 40px
        width 100%
        height 100%
    .count-wrapper
      flex 2 0
      overflow auto
      .count
        font-size 16px
        .table
          margin 0 auto
          width 80%
          text-align center
          .td
            padding 10px




</style>



